import React, { useState } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Landing from "../pages/Landingpage.jsx";

function App() {
  const [menuToggler, setMenu] = useState(false);

  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <Landing menuToggler={menuToggler} setMenu={setMenu} />
        </Route>

        <Route exact path="/latif">
          <Landing menuToggler={menuToggler} setMenu={setMenu} />
        </Route>

        <Route exact path="/latif/">
          <Landing menuToggler={menuToggler} setMenu={setMenu} />
        </Route>

        <Route exact path="/landing">
          <Landing />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
